"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import HeartCursor from "@/components/HeartCursor";

const locations = [
  {
    id: "coffee",
    name: "Tempat Ngopi",
    image: "https://images.pexels.com/photos/1855214/pexels-photo-1855214.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2"
  },
  {
    id: "cafe",
    name: "Cafe",
    image: "https://images.pexels.com/photos/2074130/pexels-photo-2074130.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2"
  },
  {
    id: "restaurant",
    name: "Restaurant",
    image: "https://images.pexels.com/photos/941861/pexels-photo-941861.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2"
  },
  {
    id: "street-food",
    name: "Street Food",
    image: "https://images.pexels.com/photos/2608517/pexels-photo-2608517.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2"
  },
  {
    id: "beach",
    name: "Beach",
    image: "https://images.pexels.com/photos/1032650/pexels-photo-1032650.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2"
  },
  {
    id: "playground",
    name: "Playground",
    image: "https://images.pexels.com/photos/2005992/pexels-photo-2005992.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2"
  }
];

export default function SchedulePage() {
  const router = useRouter();
  const [date, setDate] = useState("");
  const [time, setTime] = useState("");
  const [selectedLocation, setSelectedLocation] = useState("");
  const [error, setError] = useState("");

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!date || !time || !selectedLocation) {
      setError("Please fill in all fields");
      return;
    }

    const selectedLocationData = locations.find(loc => loc.id === selectedLocation);
    
    router.push(
      `/summary?date=${encodeURIComponent(date)}&time=${encodeURIComponent(time)}&location=${encodeURIComponent(selectedLocationData?.name || "")}&image=${encodeURIComponent(selectedLocationData?.image || "")}`
    );
  };

  return (
    <main className="min-h-screen p-8 bg-[#fdf4f5]">
      <HeartCursor />
      
      <div className="max-w-4xl mx-auto">
        <h1 className="text-4xl font-bold text-center text-[#333] mb-12">
          Bismillah Jadwalin Tanggal ketemu
        </h1>
        <br></br>
        <h2 className="text-4xl font-bold text-center text-[#333] mb-12">
          <i>Ini rencana kaloo buatt akuu benerann, untuk jadii acuann targett akuuuuuüôèüèª</i>
        </h2>

        <form onSubmit={handleSubmit} className="space-y-8">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
            <div className="space-y-4">
              <label className="block text-lg font-medium text-[#333]">
                Tanggal (aku request 3-4 bulan kedepann)
                <input
                  type="date"
                  value={date}
                  onChange={(e) => setDate(e.target.value)}
                  className="mt-2 block w-full px-4 py-3 rounded-lg border border-[#f6a5c0] 
                           focus:ring-2 focus:ring-[#f6a5c0] focus:border-transparent"
                />
              </label>
            </div>

            <div className="space-y-4">
              <label className="block text-lg font-medium text-[#333]">
                Jam (menyesuaikan aja sebenernya formlitas)
                <input
                  type="time"
                  value={time}
                  onChange={(e) => setTime(e.target.value)}
                  className="mt-2 block w-full px-4 py-3 rounded-lg border border-[#f6a5c0] 
                           focus:ring-2 focus:ring-[#f6a5c0] focus:border-transparent"
                />
              </label>
            </div>
          </div>

          <div className="space-y-4">
            <h2 className="text-2xl font-medium text-[#333]">Choose a Location</h2>
            <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-6">
              {locations.map((location) => (
                <div
                  key={location.id}
                  onClick={() => setSelectedLocation(location.id)}
                  className={`
                    cursor-pointer rounded-xl overflow-hidden shadow-lg
                    transform transition-all duration-300 hover:scale-105
                    ${selectedLocation === location.id ? 'ring-4 ring-[#f6a5c0]' : ''}
                  `}
                >
                  <div className="relative h-48">
                    <img
                      src={location.image}
                      alt={location.name}
                      className="w-full h-full object-cover"
                    />
                  </div>
                  <div className="p-4 bg-white">
                    <h3 className="text-lg font-medium text-[#333]">{location.name}</h3>
                  </div>
                </div>
              ))}
            </div>
          </div>

          {error && (
            <p className="text-red-500 text-center">{error}</p>
          )}

          <div className="text-center">
            <button
              type="submit"
              className="px-8 py-4 bg-[#f6a5c0] text-white rounded-lg font-medium 
                       shadow-lg hover:shadow-xl transform hover:scale-105 
                       transition-all duration-300"
            >
              Lanjut Yaa
            </button>
          </div>
        </form>
      </div>
    </main>
  );
}
